import React, { Component } from 'react';
import './field.css';
import Cell from "../cell/cell";

export default class Field extends Component {

    state = {
        cellPositions: [null, null, null, null, null, null, null, null, null],
        current: 'o',
        winner: false
    };

    doStep = (i) => {
        let {cellPositions, current} = this.state;
        let before = cellPositions.slice(0, i);
        let after = cellPositions.slice(i+1);
        let step;
        current === 'x' ? step = 'o' : step = 'x';
        let out = [...before, step, ...after];
        this.setState((cellPositions, current)=> {
            return {
                cellPositions: out,
                current: step,
            }
        });
    };

    checkWinner = (arr) => {
        let variants = [
            [0,1,2],
            [3,4,5],
            [6,7,8],
            [0,4,8],
            [2,4,6],
            [0,3,6],
            [1,4,7],
            [2,5,8],
        ];
        for (let i = 0; i < variants.length; i++) {
            let [a,b,c] = variants[i];
            if (arr[a] && arr[a]===arr[b] && arr[b]===arr[c]) {
                return true;
            }
        }
        return null;
    };

    render() {
        const { cellPositions, current } = this.state;
        let winner = this.checkWinner(cellPositions);
        const cells = cellPositions.map( (elem, i) => {
            return (
                <Cell
                    key={i}
                    position={elem}
                    onStep={() => this.doStep(i)}
                />
            )
        });
        let winnerField = (
            winner ? <h1>Выиграл {current}</h1> : cells
        );
        return (
            <div>
                <div className="field">
                    { winnerField }
                </div>
            </div>

        );
    }
}